import Layout from '../Components/Layout';
import fetch from 'isomorphic-unfetch';
import React from 'react';
import Forms from '../Components/Forms';

class Index extends React.Component{

getCountries = async(e) => {
    e.preventDefault();
    const api = await fetch('https://restcountries.eu/rest/v2/all');
    const data = await api.json();
    for(let i=0; i <= data.length; i++)

    console.log(`The capital of ${data[0].name} is ${data[0].capital}`)

}

    render(){

        return(
            <Layout>
            <div>
            
            <Forms getCountries={this.getCountries}
            name={this.state.name}
            alpha3code={this.state.alpha3code}
            />
            <style jsx global>{`
            body{
              background: #333;
            }
            
            `}</style>
            
            </div>
            </Layout>
        );
    }
    }

export default Index;